import React from 'react';
import styled from 'styled-components';

export default function Timeline () {

    // data
    const data = [
        {
            id: 0,
            year: 2010,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 1,
            year: 2010,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 2,
            year: 2011,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 3,
            year: 2012,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        }
    ]

    const Container = styled.div`
        display: flex;
        height: 400px;
        width: 100%;
        position: relative;
    `;

    const Column = styled.div`
        height: 100%;
        width: 75px;
        background-color: red;
        border-right: 1px solid black;
    `;

    const LineMiddle = styled.div`
        width: 100%;
        top: 50%;
        position: absolute;
        border-bottom: 1px solid black;

    `;

    const Year = styled.h4`
        position: absolute;
        top: 50%;
        margin: 0;
    `;

    function getColumn() {

        const years = {};
        return data.map((d)=>{
            const year = years[d.year] ? '' : d.year;
            years[d.year] = true;

            return (
            <Column key={d.id}>
                <Year> {year} </Year>
            </Column>
            );
        });
    }

    return (
        <Container>
            {getColumn()}
            <LineMiddle />
        </Container>
    );
}